import React from 'react'
import Link from 'gatsby-link'
import ProjectCards from '../components/ProjectCards';

const FooterRainbow = () =>
<div className="rainbow block top-slice padding-bottom-50">
  <div className="main white-text">
    <div className="footer-links-list">
      <Link to='/' className='white-text'><h2>⬅ Back to Home</h2></Link>
    </div>
    <h1 className="">Selected Work</h1>
    <ProjectCards />
  </div>
  <div className="main white-text margin-top-none">
    <h1 className="">Elsewhere</h1>
    <div className="footer-links-list bigger">
      <a target="_blank" href="https://twitter.com/willdjthrill" >Twitter</a>
      <a target="_blank" href="https://dribbble.com/willdjthrill" >Dribbble</a>
      <a target="_blank" href="https://medium.com/@willdjthrill" >Medium</a>
      <a target="_blank" href="https://instagram.com/wow_willnewton" >Instagram</a>
      <a target="_blank" href='https://williamnewton.bigcartel.com'><u>~*Store*~</u></a>
      <a target="_blank" href="https://medium.com/@willdjthrill" >Soundcloud</a>
      <a target="_blank" href="https://instagram.com/wow_willnewton" >Mixcloud</a>
    </div>
  </div>

  <div className="footer-subtext white-text text-center">
    <p>© William Newton 2020   |   Handwritten by myself using <a target="_blank" className='white-text' href="https://www.gatsbyjs.org/">GatsbyJS</a></p>
  </div>

</div>

export default FooterRainbow



