import React from 'react'
import Link from 'gatsby-link'

// Work Project Cards
import workCrossfader from '../images/crossfader-thumb.jpg'
import workFeathr from '../images/feathr-thumb.jpg'
import workBreathe from '../images/breathe-thumb.jpg'

const ProjectCards = () =>
  <div className="card-holder">
    <Link to='/crossfader'>
      <div className="card first-card">
        <img className='card-img' src={workCrossfader} />
        <div className='card-text'>
          <p className='mini'>2014-2016</p>
          <p>Crossfader - Everybody Spins</p>
        </div>
      </div>
    </Link>
    <Link to='/breathe'>
      <div className="card second-card">
        <img className='card-img' src={workBreathe} />
        <div className='card-text'>
          <p className='mini'>2015</p>
          <p>Breathe - Mindful Reminders</p>
        </div>
      </div>
    </Link>
    <Link to='/feathr'>
      <div className="card third-card">
        <img className='card-img' src={workFeathr} />
        <div className='card-text'>
          <p className='mini'>2014</p>
          <p>Feathr - Digital Networking</p>
        </div>
      </div>
    </Link>
  </div>

export default ProjectCards



